.shop {
  width: 100%;
}
.shop .isfixed {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
}
.shop .shop-chose {
  width: 100%;
  height: 10vw;
  background-color: #fff;
  border-bottom: 1px solid #ededed;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
}
.shop .shop-chose div {
  width: 50vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}
.shop .shop-chose div span {
  width: 10vw;
  height: 10vw;
  line-height: 11vw;
  font-size: 4vw;
  text-align: center;
}
.shop .shop-chose .active span {
  color: #0094ff;
  font-weight: bold;
  border-bottom: 2.5px solid #0094ff;
  height: 10vw;
  line-height: 10vw;
}



