.shop-head {
  width: 100%;
  height: 40vw;
}
.shop-head .shop-bg {
  width: 100%;
  height: 100%;
  background-image: url('../../static/images/shop/shop-bg.jpeg');
  background-position: center;
  border: 1px solid transparent;
  opacity: .8;
}
.shop-head .shop-bg .shop-back {
  width: 5vw;
  height: 5vw;
  background-image: url('../../static/images/back/back-white.svg');
  background-size: 100%;
  background-repeat: no-repeat;
  margin: 2vw 0 2vw 3vw;
}
.shop-head .shop-bg .shop-detail {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
.shop-head .shop-bg .shop-detail .shop-logo {
  width: 18vw;
  height: 18vw;
  border: 1px solid #ccc;
  background: rgba(119, 103, 137, 0.43) url('../../static/images/shop/shop-logo.jpeg') no-repeat;
  background-size: 100%;
  margin: 3vw 2vw 3vw 3vw;
}
.shop-head .shop-bg .shop-detail .shop-info {
  height: 18vw;
}
.shop-head .shop-bg .shop-detail .shop-info h3 {
  color: #fff;
}
.shop-head .shop-bg .shop-detail .shop-info p {
  font-size: 3vw;
  color: #fff;
  padding-top: 1vw;
}
.shop-head .shop-bg .shop-activity {
  display: -ms-flexbox;
  display: flex;
  color: #fff;
  font-size: 3vw;
  margin-left: 3vw;
}
.shop-head .shop-bg .shop-activity span {
  width: 3.5vw;
  height: 4.2vw;
  background-color: #0BB40B;
}
.shop-head .shop-bg .shop-activity p {
  margin-left: 2vw;
}



