.goods {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.goods .goods-sort {
  width: 30vw;
}
.goods .goods-sort .isfixed {
  position: fixed;
  top: 10vw;
  left: 0;
  z-index: 0;
}
.goods .goods-sort ul {
  width: 100%;
}
.goods .goods-sort ul li {
  width: 100%;
  height: 15vw;
  line-height: 15vw;
  padding-left: 4vw;
  font-size: 4vw;
  color: #666;
  border-bottom: 1px solid #ededed;
}
.goods .goods-look {
  width: 100%;
}



