.look-sort {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 0;
}
.look-sort .look-scroll {
  margin-bottom: 15vw;
  border-bottom: 1px solid #ededed;
}
.look-sort .look-scroll .look-items {
  width: 100%;
  color: #666;
  background-color: #fff;
  border-bottom: 1px solid #ededed;
}
.look-sort .look-scroll .look-items h3 {
  width: 100%;
  font-size: 4vw;
  padding: 1vw;
  background-color: #f1f1f1;
}
.look-sort .look-scroll .look-items .look-item {
  width: 100%;
  height: 30vw;
  border-bottom: 1px solid #ededed;
}
.look-sort .look-scroll .look-items .look-item .look-detail {
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -ms-flex-align: center;
      align-items: center;
}
.look-sort .look-scroll .look-items .look-item .look-detail .look-detail-img {
  width: 20vw;
  height: 20vw;
  background: url('../../static/images/goods/1-1.png') no-repeat center;
  background-size: 100%;
  -ms-flex-negative: 0;
      flex-shrink: 0;
}
.look-sort .look-scroll .look-items .look-item .look-detail .look-detail-text {
  width: 100%;
  height: 100%;
  padding-left: 1vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-line-pack: distribute;
      align-content: space-around;
}
.look-sort .look-scroll .look-items .look-item .look-detail .look-detail-text h3 {
  background-color: #fff;
  color: #000;
}
.look-sort .look-scroll .look-items .look-item .look-detail .look-detail-text p {
  font-size: 3vw;
  padding-right: 1.5vw;
}
.look-sort .look-scroll .look-items .look-item .look-detail .look-detail-text .look-detail-money {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.look-sort .look-scroll .look-items .look-item .look-detail .look-detail-text .look-detail-money p {
  font-size: 4vw;
  font-weight: bold;
  color: #f60;
}
.look-sort .look-scroll .look-items .look-item .look-detail .look-detail-text .look-detail-money span {
  width: 5.5vw;
  height: 5.5vw;
  padding-left: 0.1vw;
  padding-top: 0.3vw;
  line-height: 3.5vw;
  text-align: center;
  font-size: 5vw;
  font-weight: bold;
  border: 1px solid #3190e8;
  border-radius: 100%;
  color: #fff;
  background-color: #3190e8;
  margin-right: 2vw;
}



