.category {
  width: 100%;
  background-color: #fff;
}
.category .category-head {
  width: 100%;
  height: 15vw;
  background-color: #0094ff;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.category .category-head .back {
  width: 5vw;
  height: 5vw;
  background-image: url('../../static/images/back/back-white.svg');
  background-size: 100%;
  background-repeat: no-repeat;
  margin-left: 2vw;
}
.category .category-head h3 {
  color: #fff;
  margin-right: 2vw;
}
.category p {
  width: 100%;
  font-size: 4vw;
  padding: 1vw;
  text-align: center;
}



