.home-head {
  width: 100%;
  background-image: -webkit-gradient(linear, left top, right top, from(#0af), to(#0085ff));
  background-image: -webkit-linear-gradient(left, #0af, #0085ff);
  background-image: -o-linear-gradient(left, #0af, #0085ff);
  background-image: linear-gradient(90deg, #0af, #0085ff);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.home-head .head-search {
  width: 90vw;
  height: 10vw;
  background-color: #fff;
  border-radius: 1vw;
  margin-bottom: 3vw;
}
.home-head .head-search a {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 100%;
  text-decoration: none;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}
.home-head .head-search a img {
  width: 3.2vw;
  height: 3vw;
  margin-left: 2vw;
  margin-right: 2vw;
}
.home-head .head-search a p {
  font-size: 3.5vw;
  font-weight: bold;
  color: #666;
}



