.shop-footer {
  width: 100%;
  height: 12vw;
  border: 1px solid black;
  position: fixed;
  z-index: 1;
  left: 0;
  bottom: 0;
  background-color: #3d3d3f;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.shop-footer .cart {
  width: 14vw;
  height: 14vw;
  position: fixed;
  left: 2vw;
  bottom: 0;
  z-index: 2;
  background-color: #444;
  border-radius: 100%;
}
.shop-footer .cart .cart-img {
  width: 14vw;
  height: 14vw;
  background: url('../../../static/images/shop/cart.svg') no-repeat 45%;
  border-radius: 100%;
  background-size: 50%;
}
.shop-footer .footer-money {
  width: 30vw;
  height: 100%;
  margin-left: 18vw;
  padding: 1vw;
  color: #fff;
}
.shop-footer .footer-money span {
  font-size: 5vw;
}
.shop-footer .footer-money p {
  font-size: 3vw;
}
.shop-footer .goto-pay {
  width: 20vw;
  height: 12vw;
  line-height: 12vw;
  text-align: center;
  color: #fff;
  font-size: 4.5vw;
  background-color: #535356;
}



