.search-head {
  width: 100%;
  height: 15vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
}
.search-head .back {
  width: 7vw;
  height: 7vw;
  background-image: url('../../static/images/back/back.svg');
  background-size: 100%;
  background-repeat: no-repeat;
}
.search-head .search-input {
  width: 75vw;
  height: 8vw;
  display: -ms-flexbox;
  display: flex;
  background-color: #F1F1F1;
  border-radius: 1vw;
  -ms-flex-align: center;
      align-items: center;
}
.search-head .search-input img {
  width: 3.2vw;
  height: 3vw;
  margin-left: 2vw;
  margin-right: 2vw;
}
.search-head .search-input input {
  border: none;
  width: 93%;
  height: 5.5vw;
  font-size: 3.5vw;
  background-color: #F1F1F1;
}



