.Footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 15vw;
  background-color: #fff;
  display: -ms-flexbox;
  display: flex;
  border-top: 3px solid #dddddd;
}
.Footer a {
  width: 33.3333%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  color: #666666;
}
.Footer a div {
  width: 100%;
  height: 10vw;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 20%;
}
.Footer a span {
  width: 100%;
  height: 5vw;
  line-height: 3vw;
  font-size: 3vw;
  text-align: center;
}
.Footer .a-home .nav-home-active {
  background-image: url('../../static/images/nav/home-active.svg');
}
.Footer .a-home .nav-home-normal {
  background-image: url('../../static/images/nav/home-normal.svg');
}
.Footer .a-order .nav-order-active {
  background-image: url('../../static/images/nav/order-active.svg');
}
.Footer .a-order .nav-order-normal {
  background-image: url('../../static/images/nav/order-normal.svg');
}
.Footer .a-mine .nav-mine-active {
  background-image: url('../../static/images/nav/mine-active.svg');
}
.Footer .a-mine .nav-mine-normal {
  background-image: url('../../static/images/nav/mine-normal.svg');
}



